.landing-banner {
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
  color: white;
  text-align: center;
  margin-bottom: 15px;
  background-position: center 100%;
}
.landing-banner h1{
  font-size: 80px;
  color: #fd5c63;
  text-shadow: 1px 1px dimgrey;
  margin-bottom: 0px;
  font-weight: lighter;

}
.landing-banner p{
  font-size: 25px;
  margin-bottom: 60px;
  font-weight: lighter;
  color: rgba(255, 255, 255, 0.9);
}
.btn-green {
  color: white;
  padding: 25px 35px;
  border-radius: 26px;
  font-weight: bold;
  font-size: 25px;
  -webkit-transition: background 0.3s ease;
  -o-transition: background 0.3s ease;
  transition: background 0.3s ease;
  text-decoration: none;
  border: 3px solid #fd5c63;
}
.btn-green:hover {
  opacity: 0.7;
  text-decoration: none;
  color: white;
}



